import React, {useEffect, useState} from 'react';
import axios from 'axios';
import { Form ,Input,Button,Flex,message} from 'antd';
import './index.css'

const Predict = () =>{
  const [messageApi, contextHolder] = message.useMessage();
  const success = () => {
    messageApi.open({
      type: 'success',
      content: '预测成功',
    });
  };

    //设置后端返回数据状态
    const [responseData, setResponseData] = useState('');
    //设置错误状态
    const [error, setError] = useState(null);
  
    //保持input文本框的状态

    const handleSubmit = (event) => {
      // 在这里使用 Axios 发送 POST 请求，将输入框中的值发送到后端
      axios.post('/predict', event)
        .then(response => {
          success()
          // 在这里处理成功的响应并设置状态
          setResponseData(response.data);
          setError(null); // 清除可能存在的错误
        })
        .catch(error => {
          console.error('There was a problem with your Axios operation:', error);
          setError('There was a problem with your Axios operation'); // 设置错误状态
          setResponseData(null); // 清除可能存在的数据
        });
    };
   
    return(
      <div className='predict'>
         {contextHolder}
        <h2>模型预测</h2>
        <Form onFinish={handleSubmit} style={{height:40}}> 
          <Flex vertical = {false} gap='middle'>
            <Form.Item name='str'>
              <Input size = 'large' placeholder="请输入句子" style={{width:300,}}/>
            </Form.Item>
            <Form.Item>
              <Button  type="primary" htmlType="submit" size='large'>
                预测
              </Button>
            </Form.Item>
          </Flex>
        </Form>
        <div className='predict-result'>
          <p><span style={{fontWeight: 'bold'}}>结果:</span>{responseData}</p>
        </div>
      </div>
    )
  }

export default Predict